import React, { Component } from 'react';
import { Container, Row, Col, Button } from "react-materialize";
import { Link } from "react-router-dom";

const props = {
    loggedIn: true
}

class Home extends Component {
    render() {
        return (
        <Container className="center-align">
            <Row>
                <br/>
                <Col s={6} offset="s3">
                    <img id="home-logo" alt="yoso logo" src="./img/Yoso-Logo-Large-Text-White.svg"></img>            
                </Col>
            </Row>
            {props.loggedIn ? 
                <Row className="center-align">
                    <Col s={6} l={4}><Link to="/lists"><Button id="home-list" className="home-btn btn-large">LISTS</Button></Link></Col>
                    <Col s={6} l={4}><Link to="/pantry"><Button id="home-pantry" className="home-btn btn-large">PANTRY</Button></Link></Col>
                    <Col s={6} l={4}><Link to="/waste"><Button id="home-waste" className="home-btn btn-large">WASTE</Button></Link></Col>
                    <Col s={6} l={4}><Link to="/recipes"><Button id="home-recipes" className="home-btn btn-large">RECIPES</Button></Link></Col>
                    <Col s={6} l={4}><Link to="/account"><Button id="home-account" className="home-btn btn-large">ACCOUNT</Button></Link></Col>
                    <Col s={6} l={4}><Link to="/logout"><Button id="home-logout" className="home-btn btn-large">LOGOUT</Button></Link></Col>
                </Row>
                :
                <Row>
                    <Link to="/">
                        <Col s={6} l={6}><Link to="/login"><Button id="home-login" className="home-btn btn-large">LOGIN</Button></Link></Col>
                    </Link>
                    <Link to="/signup">
                        <Col s={6} l={6}><Link to="/signup"><Button id="home-signup" className="home-btn btn-large">SIGN-UP</Button></Link></Col>
                    </Link>
                </Row>
            }
        </Container>
        );
    }
}

export default Home;