import React, { Component } from 'react';
import { Container, Row, Col, Input, Icon, Card } from 'react-materialize';
import axios from "axios";

class SignUp extends Component {
  state = {

  }

  handleInputChange = e => {
    const { name, value } = e.target;
    this.setState({
        [name]: value
    });
};

handleSubmit = e => {
  e.preventDefault();
  axios.post('/login', {
    first: this.state.first,
    last: this.state.last,
    username: this.state.username,
    email: this.state.email,
    password: this.state.password,
    address: this.state.address
  }).then(response => {
    console.log(response)
    if(response.data) {
      console.log("Successful Signup!");
      this.setState({
        redirectTo: "/login"
      });
    } else {
      console.log("Signup Error!");
    }
  }).catch(err => {
    console.log("Signup Server Error");
    console.log(err);
  })
}

  render() {
    return (
      <Container className="center-align">
        <br/>
        <h1 className="white-text">CREATE AN ACCOUNT</h1>
        <br/>
        <Container>
          <form>
            <Card id="signup-form">
              <Row>
                <Col s={6}><Input s={12} onChange={this.handleInputChange} type="text" label="First Name" name="first" validate></Input></Col>
                <Col s={6}><Input s={12} onChange={this.handleInputChange} type="text" label="Last Name" name="last" validate></Input></Col>
              </Row>
              <Row>
                <Col s={12}><Input s={12} onChange={this.handleInputChange} offset="s3" type="text" label="Street Address" name="street" validate><Icon>location_on</Icon></Input></Col>
                <Col s={12}>
                  <Input s={4} onChange={this.handleInputChange} offset="s3" type="text" label="City" name="city" validate></Input>
                  <Input s={4} onChange={this.handleInputChange} offset="s3" type="text" label="State" name="state" validate></Input>
                  <Input s={4} onChange={this.handleInputChange} offset="s3" type="number" label="Zip Code" name="zip" validate></Input>
                </Col>
              </Row>
              <Row>
                <Col s={6}><Input s={12} onChange={this.handleInputChange} label="Email" type="email" name="email" validate><Icon>email</Icon></Input></Col>
                <Col s={6}><Input s={12} onChange={this.handleInputChange} label="Password" type="password" name="password" validate><Icon>lock</Icon></Input></Col>
              </Row>
              <Row>
                <Col s={12}><Input s={12} onClick={this.handleSubmit} className="btn btn-large" type="submit"></Input></Col>
              </Row>
            </Card>  
          </form>
        </Container>
      </Container>
    );
  }
}

export default SignUp;